import React from 'react';
import { Link } from 'react-router-dom';

export default function makeNotifMessage (notification) {
  switch (notification.type) {
    case 'REQUEST':
      notification.message = (
        <p className="notification_message">
          <b> {notification.applicant} </b>    
          <Link to={`/event/${notification.event._id}`}>
            <b> {notification.event.title} </b>
          </Link>
            <b>{notification.job}</b>  
        </p>
      );
      break;
    case 'ACCEPT':
      notification.message = (
        <p className="notification_message">
             <b> {notification.job} </b>
           
          <Link to={`/event/${notification.event._id}`}>
            <b> {notification.event.title} </b>
          </Link>
            
        </p>
      );
      break;
    case 'REJECT':
      notification.message = (
        <p className="notification_message">
                
          <Link to={`/event/${notification.event._id}`}>
            <b> {notification.event.title} </b>
          </Link>
              
          <b>    </b>
        </p>
      );
      break;
    case 'INFORMATION':
      notification.message = (
        <p className="notification_message">
               
          <Link to={`/event/${notification.event._id}`}>
            <b> {notification.event.title} </b>
          </Link>
           
        </p>
      );
      break;
    default:
      notification.message = <p>   </p>;
      break;
  }
  return notification;
}
